const Content = styled.div`
  background: white;
`;

export default class Orbitals extends React.Component{
  state = {
    mount: 'archive',
  };

  handleIconClick = val => {
    this.setState({ mount: val });
  }

  render() {
    let component = <div></div>
    if (this.state.mount === "archive") {
      component = <Archive />
    }
    return (
    <div>
      <Space>
        <MainOrbit>
          <Sun />
          <NorthPlanet data-position="bottom" data-tooltip="Hey"/>    
          <EastPlanet />
          <SouthPlanet />
          <WestPlanet />
        </MainOrbit>
      </Space>
      <Content>
        {component}
      </Content>
    </div>
    );
  }
}
